@import url("https://fonts.googleapis.com/css2?family=Oxygen&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Oxygen&display=swap");
:root {
  --main-color: rgb(68, 148, 74);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
}

.quote_machine_box {
  padding: 25px 30px 50px 30px;
  border-radius: 10px;
  background-color: white;
  width: 550px;
  min-height: 220px;
  color: var(--main-color);
  transition: color 3s;
  position: relative;
}

.quote_text {
  font-family: "Oxygen", sans-serif;
  font-size: 30px;
}

.author {
  text-align: right;
  margin: 15px;
  font-family: "Dancing Script", cursive;
  font-size: 20px;
}

.quote_button {
  border: 0px;
  padding: 10px;
  color: white;
  font-size: large;
  font-weight: 700;
  align-self: end;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.App,
.twitter_link,
.tumblr_link,
.quote_button {
  background-color: var(--main-color);
  transition: background-color 3s;
}

.twitter_link,
.tumblr_link,
.quote_button {
  border-radius: 5px;
  height: 42px;
}

.twitter_link:hover,
.tumblr_link:hover,
.quote_button:hover {
  opacity: 0.8;
}

.tumblr_link,
.twitter_link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
}

.tumblr_link {
  position: absolute;
  bottom: 10px;
  left: 80px;
}

.twitter_link {
  position: absolute;
  bottom: 10px;
  left: 20px;
}

.twitter_icon,
.tumblr_icon {
  width: 20px;
  height: 20px;
}

.shadow,
.shadow.entered {
  opacity: 1;
  transition: opacity 1s;
}

.shadow.entering,
.shadow.exiting,
.shadow.exited {
  opacity: 0;
}
